import { useEffect, useState } from 'preact/hooks';
import { PurchaseOrderModel } from '../data/models';
import { events } from '@adobe-commerce/event-bus';

export const usePurchaseOrderData = () => {
  const [poDataLoading, setPoDataLoading] = useState(true);
  const [purchaseOrderData, setPurchaseOrderData] =
    useState<PurchaseOrderModel | null>(null);

  useEffect(() => {
    const event = events.on(
      'purchase-order/data',
      (po: PurchaseOrderModel) => {
        if (po?.uid) {
          setPurchaseOrderData(po);
        }

        setPoDataLoading(false);
      },
      { eager: true }
    );

    return () => {
      event?.off();
    };
  }, []);

  return { poDataLoading, purchaseOrderData };
};
